var that;
import {
	api
} from '../../../js/api.js'

import qLoading from '../../../compontents/qLoading.vue'
export default {
	props: {
		comicid: {

		},
		// 内部调用,不通过外部调用直接跳转到漫画详情
		internalCall: {
			default: false
		}
	},
	components: {
		qLoading
	},
	data() {
		return {
			comicinfo: {
				comic_name: '加载中...',
				comic_author: ''
			},
		};
	},
	mounted() {
		var that = this;
		// console.log(this.comicid)
		api.getDetails(this.comicid).then(data => {
			setTimeout(() => {
				that.comicinfo = data;
				// console.log(that.comicinfo)
			}, 500)
		})
	},
	methods: {
		clickItem() {
			if (this.internalCall) uni.navigateTo({
				url: '/pages/comicDetails/comicDetails?id=' + this.comicid
			})
			else this.$emit('goDetail', this.comicid);
		}
	},
	computed: {

	}
}
